import React, { Component } from 'react';

// UI library

import Jumbotron from 'react-bootstrap/Jumbotron';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import Badge from 'react-bootstrap/Badge';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Form from 'react-bootstrap/Form';
import Fade from 'react-bootstrap/Fade';

import Modal from 'react-bootstrap/Modal';

import YouTube from 'react-youtube';

// non-UI library

import renderHTML from 'react-render-html';

// CSS

import './App.css';

// Settings

import { itemList, getTags, getMonths } from './Data';

const youtubeOpts = {
  height: '100%',
  width: '100%',
  playerVars: { // https://developers.google.com/youtube/player_parameters
    autoplay: 1,
    disablekb: 1,
    rel: 0,
  }
};

// Code

class App extends Component {

  constructor(props) {
    super(props);

    let tags = getTags();
    let ts = {}
    for (let tag of tags) {
      ts[tag] = true;
    }

    let months = getMonths();
    let ms = {}
    for (let month of months) {
      ms[month] = true;
    }

    this.state = {
      showYoutube: false,
      youtubeId: undefined,
      tags: ts,
      months: ms,
    };
  }

  // ----- UI interaction -----

  getOpenYoutube = (id) => () => {
    this.setState({
      youtubeId: id,
      showYoutube: true,
    })
  }

  handleCloseYoutube = () => {
    this.setState({
      showYoutube: false,
    })
  }

  handleCheckTags = (tag) => () => {
    let tags = this.state.tags
    tags[tag] = !tags[tag]
    this.setState({tags})
  }

  handleCheckAllTags = () => {
    let tags = this.state.tags
    Object.keys(tags).forEach((tag) => { tags[tag] = true })
    this.setState({tags})
  }

  handleUncheckAllTags = () => {
    let tags = this.state.tags
    Object.keys(tags).forEach((tag) => { tags[tag] = false})
    this.setState({tags})
  }

  handleCheckMonths = (month) => () => {
    let months = this.state.months
    months[month] = !months[month]
    this.setState({months})
  }

  handleCheckAllMonths = () => {
    let months = this.state.months
    Object.keys(months).forEach((month) => { months[month] = true })
    this.setState({months})
  }

  handleUncheckAllMonths = () => {
    let months = this.state.months
    Object.keys(months).forEach((month) => { months[month] = false})
    this.setState({months})
  }

  // ----- UI display -----

  /*
    info.imgUrl     : string
    info.title      : string
    info.description: string
    info.tags       : array of string
    info.date       : moment obj
    info.button     : {label, action}
  */
  createBaseCard = (info) => {
    let imgStyle = {
      height: '300px',
      backgroundPosition: 'center center', 
      backgroundRepeat: 'no-repeat',
      backgroundImage: `url(${info.imgUrl})`,
      cursor: 'pointer',

    }
    if (info.imgHeight)
      imgStyle.height = `${info.imgHeight}px`

    return (
      <Card bsPrefix="card mb-4">
        <div className="card-img-bg" style={imgStyle} onClick={info.button.action}/>
        <Card.Body>
          <div className="d-flex justify-content-between">
            <Card.Title>{info.title}</Card.Title>
            <small className="text-muted">{info.date.format("YYYY-MM-DD")}</small>
          </div>
          <Card.Text>{renderHTML(info.description)}</Card.Text>
          <div className="d-flex justify-content-between align-items-center">
            <Button size="sm" variant="outline-primary" onClick={info.button.action}>{info.button.label}</Button>
            <div>
              {
                info.tags.map((e, i) => <Badge key={i} pill variant="secondary">{e}</Badge>)
              }
              <Badge pill variant="info">{info.date.format("YYYY-MM")}</Badge>
            </div>
          </div>
        </Card.Body>
      </Card>
    )
  }

  createCard = (info, index) => {
    let table = {
      youtube: (info) => {
        info.imgUrl = `https://img.youtube.com/vi/${info.youtubeId}/0.jpg`;
        info.imgHeight = 270;
        info.button = { label: 'Youtube', action: this.getOpenYoutube(info.youtubeId)};
        return this.createBaseCard(info);
      },
      image: (info) => {
        let openImage = () => {
          if (info.fullImgUrl)
            window.location = info.fullImgUrl;
        }
        info.imgHeight = info.imgHeight || 300;
        info.button = { label: 'Detail Image', action: openImage};
        return this.createBaseCard(info);
      },
    };

    let tagCheck = info.tags.map((tag)=>this.state.tags[tag]).reduce((a, b) => a || b);
    let dateCheck = this.state.months[info.date.format("YYYY-MMM")] === true;

    // console.log(`${tagCheck}, ${dateCheck}`)
    let show = tagCheck && dateCheck;
    let generator = table[info.type];
    if (generator) {
      return (
        <Fade key={index} in={show} mountOnEnter unmountOnExit>
        <Col md={6} key={index}>
          {generator(info)}
        </Col>
        </Fade>
      )
    }
  }

  render() {

    return (
      <div className="App bg-light">
        <Jumbotron>
          <div>
            <h1 className="jumbotron-heading text-outlined">Hexapod Project</h1>
            <ul>
              <li className="jumbotron-text text-outlined">6 legs, each with 3 servos, total 18 degress of freedom</li>
              <li className="jumbotron-text text-outlined">Android / iOS remotely controlled</li>
              <li className="jumbotron-text text-outlined">2S LiPO battery powered</li>
            </ul>
            <p>
              <Button variant="primary" target="_blank" href="https://github.com/SmallpTsai/hexapod-v2-7697">Github</Button>
              <Button variant="primary" target="_blank" href="https://www.youtube.com/channel/UC4nvIRRByxOkgKwkdb99Z8w">Youtube</Button>
            </p>
          </div>
        </Jumbotron>

        <Container>
          <Navbar bg="light" variant="light">
            <Navbar.Brand href="#home">Filter</Navbar.Brand>
            <Nav className="mr-auto">
  
              <NavDropdown title="Tag">
                {
                  Object.keys(this.state.tags).map((tag, i) => (
                    <NavDropdown.Item key={i} id={tag} onClick={this.handleCheckTags(tag)}>
                      <Form.Check type="checkbox" readOnly checked={this.state.tags[tag]} label={tag}/>
                    </NavDropdown.Item>
                  ))
                }
                <NavDropdown.Divider />
                <NavDropdown.Item onClick={this.handleCheckAllTags}>all</NavDropdown.Item>
                <NavDropdown.Item onClick={this.handleUncheckAllTags}>clear</NavDropdown.Item>
              </NavDropdown>

              <NavDropdown title="Date">
                {
                  Object.keys(this.state.months).map((month, i) => (
                    <NavDropdown.Item key={i} id={month} onClick={this.handleCheckMonths(month)}>
                      <Form.Check type="checkbox" readOnly checked={this.state.months[month]} label={month}/>
                    </NavDropdown.Item>
                  ))
                }
                <NavDropdown.Divider />
                <NavDropdown.Item onClick={this.handleCheckAllMonths}>all</NavDropdown.Item>
                <NavDropdown.Item onClick={this.handleUncheckAllMonths}>clear</NavDropdown.Item>
              </NavDropdown>

            </Nav>
          </Navbar>
        </Container>

        <div className="py-5 bg-light">
          <Container>
            <Row>
              {
                itemList.map((info, index) => this.createCard(info, index))
              }
            </Row>
          </Container>
        </div>

        <footer className="text-muted">
          <Container>
            <small><p>This is a personal hobby project. <br/>All source code (including 3D components, circuit diagrams, source code) are opened under GPL license. <br/>Feel free to make one by yourself. <br/>Happy Making!</p></small>
          </Container>
          <Container>
            <p className="float-right">
              <a href="#top">Back to top</a>
            </p>
            <small><p>Powered by &copy; Bootstrap</p></small>
          </Container>
        </footer>



        <Modal 
          show={this.state.showYoutube} 
          onHide={this.handleCloseYoutube}
          dialogClassName="youtube-dialog"
          centered
        >
          <div className="youtube-wrapper">
            <YouTube
              className="youtube-iframe"
              videoId={this.state.youtubeId}
              opts={youtubeOpts}
            />
          </div>
        </Modal>

      </div>
    );
  }
}

export default App;
