export function getTags() {
  let result = new Set();
  for (let i of itemList) {
    for (let t of i.tags) {
      result.add(t)
    }
  }
  result = Array.from(result);
  result.sort();
  return result;
}

export function getMonths() {
  let result = new Set();
  for (let i of itemList) {
    result.add(i.date.format("YYYY-MM"));
  }
  result = Array.from(result);
  result.sort().reverse()
  return result.map((m) => moment(m, "YYYY-MM").format("YYYY-MMM"));
}
